import { DatePipe } from '@angular/common';
import { Component, Inject, OnInit } from '@angular/core';
import { LichSuDatChoModel } from '../../models/lich-su-dat-cho.model';
import { HuyYeuCauService } from './huy-yeu-cau.service';
import {
  MatDialogRef,
  MAT_DIALOG_DATA,
  MatDialog,
} from '@angular/material/dialog';
import { ThietBiChiTietModel } from '../../models/thiet-bi-chi-tiet.model';
import { AlertDialogComponent } from '../../pages/alert-dialog/alert-dialog.component';
import { MatSnackBar } from '@angular/material/snack-bar';
import { FormGroup, FormControl } from '@angular/forms';
import { environment } from 'src/environments/environment';
import { Router } from '@angular/router';
@Component({
  selector: 'app-huy-yeu-cau',
  templateUrl: './huy-yeu-cau.component.html',
  styleUrls: ['./huy-yeu-cau.component.scss'],
})
export class HuyYeuCauComponent implements OnInit {
  _yeuCau = new LichSuDatChoModel();
  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogMainRef: MatDialogRef<HuyYeuCauComponent>,
    private __service: HuyYeuCauService,
    public dialog: MatDialog,
    private _alertDialog: MatDialog,
    private __router: Router,
    private __snackbar: MatSnackBar,
    public datepipe: DatePipe
  ) {}
  _title = 'Hủy yêu cầu';
  _titleMainButton = 'Hủy';
  _thietBiChiTiet: ThietBiChiTietModel[];
  _minDate = new Date();
  _maxDate = new Date();
  _hasValueStartDay = false;
  _hasValueEndDay = false;
  campaignTwo: FormGroup;
  showProgressBar = true;
  __isClient = false;
  ngOnInit(): void {
    this._maxDate.setDate(this._minDate.getDate() + 30);
    if (this.data) {
      if (this.data.yeuCau) {
        this._yeuCau = this.data.yeuCau;
        this.campaignTwo = new FormGroup({
          start: new FormControl(new Date(this._yeuCau.NgayBatDau)),
          end: new FormControl(new Date(this._yeuCau.NgayKetThuc)),
        });
      }
      if (this.data.isClient) this.__isClient = this.data.isClient;
    }
    this.getDataThietBiChiTiets();
  }

  private getDataThietBiChiTiets() {
    this.showProgressBar = true;

    this.__service.getThietBiChiTiet(this._yeuCau.IDThietBi).subscribe(
      (data) => {
        this.showProgressBar = false;
        if(data.Message){
          this.__snackbar.open(data.Message, null, {
            duration: 2000,
          });
          return
        }
        this._thietBiChiTiet = data;
      },
      (error) => {
        this.showProgressBar = false;
      }
    );
  }

  approve() {
    console.log(this.__isClient);
    if (
      this._yeuCau.LyDoHuy == null ||
      this._yeuCau.LyDoHuy.trim().length == 0
    ) {
      this.__snackbar.open('Vui lòng nhập lý do', null, {
        duration: 3000,
      });
    } else if (this.__isClient) {
      this.showProgressBar = true;
      this.__service.kiemTra(this._yeuCau).subscribe(
        (data) => {
          this.showProgressBar = false;
          if (data === 'true') {
            this.cancel();
          } else {
            var dialogRef = this._alertDialog.open(AlertDialogComponent, {
              disableClose: true,
              width: '17rem',
              data: {
                dataKey: `${data}. Bạn có chắc muốn hủy yêu cầu: ${this._yeuCau.MaDatCho}?`,
                isConfirm: true,
              },
            });
            dialogRef.afterClosed().subscribe((res) => {
              if (res) {
                this.cancel();
              }
            });
          }
        },
        (error) => {
          this.dialogMainRef.close({
            result: false,
          });
          this.showLogin();
          this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
            duration: 2000,
          });
        }
      );
    } else {
      this.confirm();
    }
  }
  showLogin() {
    if (this.__isClient) this.__router.navigate(['login']);
    else this.__router.navigate(['admin/login']);
  }
  confirm() {
    var dialogRef = this._alertDialog.open(AlertDialogComponent, {
      disableClose: true,
      width: '17rem',
      data: {
        dataKey: 'Bạn có chắc muốn hủy yêu cầu: ' + this._yeuCau.MaDatCho + '?',
        isConfirm: true,
      },
    });

    dialogRef.afterClosed().subscribe((res) => {
      if (res) {
        this.cancel();
      }
    });
  }
  cancel() {
    this.showProgressBar = true;
    this.__service.cancelDatCho(this._yeuCau).subscribe(
      (data) => {
        this.showProgressBar = false;
        if (data === 'true') {
          this.__snackbar.open('Hủy yêu cầu thành công', null, {
            duration: 2000,
          });
          this.dialogMainRef.close({
            result: true,
          });
        } else {
          this.__snackbar.open(data.Message, null, {
            duration: 2000,
          });
        }
      },
      (error) => {
        this.dialogMainRef.close({
          result: false,
        });
        this.showLogin();
        this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
          duration: 2000,
        });
      }
    );
  }
  close() {
    var dialogRef = this._alertDialog.open(AlertDialogComponent, {
      disableClose: true,
      width: '17rem',
      data: {
        dataKey: 'Thay đổi của bạn sẽ không được lưu. Bạn có muốn hủy?',
        isConfirm: true,
      },
    });
    dialogRef.afterClosed().subscribe((res) => {
      if (res) {
        this.dialogMainRef.close({
          result: false,
        });
      }
    });
  }
}
