import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../../environments/environment';
import { LichSuDatChoModel } from '../../models/lich-su-dat-cho.model';
@Injectable({ providedIn: 'root' })
export class HuyYeuCauService {
  constructor(private httpClient: HttpClient) {}
  cancelDatCho(input: LichSuDatChoModel): Observable<any> {
    let url = environment.api.url + 'LichSuDatCho/huyYeuCau';
    return this.httpClient.post(url, input);
  }
  kiemTra(input: LichSuDatChoModel): Observable<any> {
    let url =
      environment.api.url + 'LichSuDatCho/kiemTra';
    return this.httpClient.post(url, input);
  }
  getThietBiChiTiet(maThietBi: string): Observable<any> {
    let url = environment.api.url + 'ThietBiChiTiet/get?MaThietBi=' + maThietBi;
    return this.httpClient.get(url);
  }
}
