import { Injectable } from '@angular/core';
import { environment } from '../../../environments/environment';
@Injectable({ providedIn: 'root' })
export class AuthService {
  constructor() {}

  getToken(isAdmin: boolean) {
    return isAdmin
      ? localStorage.getItem(environment.key_storage.TOKEN.ADMIN) || null
      : localStorage.getItem(environment.key_storage.TOKEN.CLIENT) || null;
  }

  setToken(token: string, isAdmin: boolean) {
    isAdmin
      ? localStorage.setItem(environment.key_storage.TOKEN.ADMIN, token)
      : localStorage.setItem(environment.key_storage.TOKEN.CLIENT, token);
  }

  isAuth(isAdmin: boolean) {
    return Boolean(this.getToken(isAdmin));
  }
}
