
import {  formatDate } from '@angular/common';
export class LichSuDatChoModel {
    MaDatCho: string
    MaNguoiHoc: string
    TenNguoiHoc: string
    MaThietBi: string
    IDThietBi: string
    TenThietBi: string
    MaGiaoVien: string
    TenGiaoVien: string
    NgayHetHan:  string
    NgayHetHanDate: Date
    NgayBatDau: string
    NgayKetThuc: string
    NgayBatDauDate: Date
    NgayKetThucDate: Date
    MaThietBiChiTiet: string
    IDThietBiChiTiet: string
    NguoiHuy: string
    TenNguoiHuy: string
    NgayHuy: string
    NgayHuyDate: Date
    LyDoHuy: string
    IsCancel: boolean
}
export class LichSuDatChoRequestModel{
    MaDatCho: string
    MaNguoiHoc: string
    MaGiaoVien: string
    NgayHetHan: string
    MaNoiQuanLy: string
    NgayBatDau: string
    NgayKetThuc: string
    MaThietBiChiTiet: string
    constructor() {
        this.MaNoiQuanLy = ""
        this.MaDatCho = ''
        this.MaNguoiHoc = ''
        this.MaThietBiChiTiet = ''
        this.MaGiaoVien = ''
        this.NgayHetHan = formatDate(new Date(), "yyyyyyyy-MM-dd'T'HH:mm:ss", 'en')
        this.NgayBatDau = formatDate(new Date(), "yyyyyyyy-MM-dd'T'HH:mm:ss", 'en')
        this.NgayKetThuc = formatDate(new Date(), "yyyyyyyy-MM-dd'T'HH:mm:ss", 'en')
        
    }
}